/* SG
# Spinner

You may add spinners by adding a `spinner` class.

```
<p class="spinner"></p>
<p class="spinner-dotted"></p>
```
*/

// Spinner styles
.spinner,
.spinner-dotted {
	@include row-inline;
	
	animation: spinner 2s linear infinite;
	border-radius: 50%;
	height: rem(120);
	width: rem(120);
}

.spinner {
	border-color: color(lt-gray); 
	border-left-color: color(blue);
	border-style: solid;
	border-width: rem(16);
}

.spinner-dotted {
	border-bottom-color: color(lt-blue);
	border-bottom-width: rem(12);
	border-left-color: #3399ff;
	border-left-width: rem(8);
	border-right-color: color(blue);
	border-right-width: rem(14);
	border-top-color: color(dk-blue);
	border-top-width: rem(18);
	border-style: dotted;
}

@keyframes spinner {
	from {
		transform: rotate(0deg);
	}

	to {
		transform: rotate(360deg);
	}
}
