/* SG
# Button

Buttons are styled with `.btn-*` classes.

```
<button class="btn-xs bg-dk-blue text-white bg-hover-blue" type="button">xs</button>
<button class="btn-sm bg-dk-blue text-white bg-hover-blue" type="button">sm</button>
<button class="btn-md bg-dk-blue text-white bg-hover-blue" type="button">md</button>
<button class="btn-lg bg-dk-blue text-white bg-hover-blue" type="button">lg</button>
<button class="btn-xl bg-dk-blue text-white bg-hover-blue" type="button">xl</button>
<button class="btn-full bg-dk-blue text-white bg-hover-blue" type="button">full</button>
```
*/

/* SG
# Button/Rounded

Buttons can be rounded with the `.rounded` class.

```
<button class="btn-xs rounded bg-dk-blue text-white bg-hover-blue" type="button">xs</button>
<button class="btn-sm rounded bg-dk-blue text-white bg-hover-blue" type="button">sm</button>
<button class="btn-md rounded bg-dk-blue text-white bg-hover-blue" type="button">md</button>
<button class="btn-lg rounded bg-dk-blue text-white bg-hover-blue" type="button">lg</button>
<button class="btn-xl rounded bg-dk-blue text-white bg-hover-blue" type="button">xl</button>
<button class="btn-full rounded bg-dk-blue text-white bg-hover-blue" type="button">full</button>
```
*/

/* SG
# Button/States

Buttons can be disabled with the `disabled` attribute or `.disabled` class.

```
<button class="btn-md" type="button" disabled>disabled</button>
<button class="btn-md disabled" type="button">disabled</button>
```
*/

/* SG
# Button/Group

Buttons can be grouped with `.btn-group-*` classes.

```
<section class="btn-group-row" role="group" aria-label="button row group">
	<button class="btn-md bg-dk-blue text-white bg-hover-blue" type="button">md</button>
	<button class="btn-md bg-dk-blue text-white bg-hover-blue" type="button">md</button>
	<button class="btn-md bg-dk-blue text-white bg-hover-blue" type="button">md</button>
	<button class="btn-md bg-dk-blue text-white bg-hover-blue" type="button">md</button>
	<button class="btn-md bg-dk-blue text-white bg-hover-blue" type="button">md</button>
</section>
<section class="btn-group-col" role="group" aria-label="button column group">
	<button class="btn-md bg-dk-blue text-white bg-hover-blue" type="button">md</button>
	<button class="btn-md bg-dk-blue text-white bg-hover-blue" type="button">md</button>
	<button class="btn-md bg-dk-blue text-white bg-hover-blue" type="button">md</button>
	<button class="btn-md bg-dk-blue text-white bg-hover-blue" type="button">md</button>
	<button class="btn-md bg-dk-blue text-white bg-hover-blue" type="button">md</button>
</section>
<section class="btn-group-full" role="group" aria-label="button full row group">
	<button class="btn-md bg-dk-blue text-white bg-hover-blue" type="button">md</button>
	<button class="btn-md bg-dk-blue text-white bg-hover-blue" type="button">md</button>
	<button class="btn-md bg-dk-blue text-white bg-hover-blue" type="button">md</button>
	<button class="btn-md bg-dk-blue text-white bg-hover-blue" type="button">md</button>
	<button class="btn-md bg-dk-blue text-white bg-hover-blue" type="button">md</button>
</section>
```
*/

// Button styles
.btn-xs,
.btn-sm,
.btn-md,
.btn-lg,
.btn-xl,
.btn-full {
	@include row-inline;

	justify-content: center;
	margin-bottom: rem(16);
	margin-right: rem(16);

	&.rounded {
		border-radius: rem(24);
	}
}

// Button sizes
.btn-xs {
	padding: rem(8 10);
}

.btn-sm {
	padding: rem(10 20);
}

.btn-full,
.btn-md {
	padding: rem(12 30);
}

.btn-lg {
	padding: rem(14 40);
}

.btn-xl {
	padding: rem(16 50);
}

.btn-full {
	width: 100%;
}

// Button groups
.btn-group-col,
.btn-group-full,
.btn-group-row {
	@include row;

	padding-bottom: rem(16);
	padding-top: rem(16);

	& .btn-xs,
	& .btn-sm,
	& .btn-md,
	& .btn-lg,
	& .btn-xl {
		border-bottom: rem(1) solid color(white);
		border-left: rem(1) solid color(white);
		margin: 0;
	}
}

.btn-group-col {
	@include column;
}

.btn-group-full {
	width: 100%;

	& .btn-xs,
	& .btn-sm,
	& .btn-md,
	& .btn-lg,
	& .btn-xl {
		@include flex(1, 0, auto);
	}
}

