/* SG
# Card

Cards are styled with `.cards` and `.card` classes.

```
<section class="card">
	<p class="card-text">card</p>
</section>
<section class="card">
	<p class="card-text">card</p>
</section>
<section class="card">
	<p class="card-text">card</p>
</section>
```
*/

// Card styles
.card {
    @include row-inline;
	@include shadow(1);

    border: rem(1) solid color(gray);
    flex-direction: column;
	margin: rem(8);
	min-width: rem(400);
	padding: rem(16);
}

